import Vue from 'vue'
import Vuex from 'vuex'

// 1.安装插件
Vue.use(Vuex)

// 2.创建对象

const store = new Vuex.Store({
  state: {
    user: {
      username: '',
      password: '',
      loginRole: ''
    },
    menuList: [[{
      id: 100,
      authName: '我的信息',
      path: 'administrator',
      children: [{
        id: 101,
        authName: '基本信息',
        path: 'administrator',
        children: []
      }]
    }, {
      id: 200,
      authName: '权限管理',
      path: 'roles',
      children: [{
        id: 201,
        authName: '权限列表',
        path: 'roles',
        children: []
      }]
    }, {
      id: 300,
      authName: '审核列表',
      path: 'censor',
      children: [{
        id: 301,
        authName: '待审核应用',
        path: 'censor',
        children: []
      }]
    }], [{
      id: 400,
      authName: '我的信息',
      path: 'developer',
      children: [{
        id: 401,
        authName: '基本信息',
        path: 'developer',
        children: []
      }]
    }, {
      id: 500,
      authName: '应用管理',
      path: 'apps',
      children: [{
        id: 501,
        authName: '应用列表',
        path: 'apps',
        children: []
      }, {
        id: 502,
        authName: '修改参数',
        path: 'params',
        children: []
      }]
    }]],
    loginRole: '0',
    Types: [{
      value: '选项1',
      label: '商城'
    }, {
      value: '选项2',
      label: '游戏'
    }, {
      value: '选项3',
      label: '影音'
    }, {
      value: '选项4',
      label: '社交'
    }, {
      value: '选项5',
      label: '资讯'
    }, {
      value: '选项6',
      label: '学习'
    }, {
      value: '选项7',
      label: '工具'
    }]
  },
  mutations: {
    // 方法
    setLoginRole (state, role) {
      state.loginRole = role
    },
    setUser (state, user) {
      state.user = user
      state.user.loginRole = state.loginRole
    }
  },
  actions: {},
  getters: {
    getMenu (state) {
      if (state.loginRole === '0') {
        return state.menuList[0]
      } else if (state.loginRole === '1') {
        return state.menuList[1]
      } else {
        console.log('获取列表失败')
        return []
      }
    }
  },
  modules: {}
})

// 3.导出store独享
export default store
