const Register = () => import(/* webpackChunkName: "login_home_welcome" */ '../components/Register.vue')
const Home = () => import(/* webpackChunkName: "login_home_welcome" */ '../components/Home.vue')
const Welcome = () => import(/* webpackChunkName: "login_home_welcome" */ '../components/Welcome.vue')
const Roles = () => import(/* webpackChunkName: "user_rights_roles" */ '../components/Power/Roles.vue')
const Params = () => import(/* webpackChunkName: "params_goods_add_reports" */ '../components/Goods/Params.vue')
const Apps = () => import(/* webpackChunkName: "params_goods_add_reports" */ '../components/Goods/Apps.vue')
const Add = () => import(/* webpackChunkName: "params_goods_add_reports" */ '../components/Goods/Add.vue')
const CensorList = () => import(/* webpackChunkName: "appList_userpage" */ '../components/Censor/CensorList.vue')
const Developer = () => import(/* webpackChunkName: "appList_userpage" */ '../components/Mainpage/Developer.vue')
const Administrator = () => import(/* webpackChunkName: "appList_userpage" */ '../components/Mainpage/Administrator.vue')
